html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background-color: #ffffff;
}

.nav-button {
  background-color: transparent;
  border: none;
}

nav {
  width: 100%;
  height: 100px;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
nav .nav-right .nav-buttons-wrapper {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-self: center;
}

.product-section {
  background: linear-gradient(to bottom, transparent, transparent 80%, #5ccfd3 80%, #5ccfd3 100%);
}
.product-section .container .title-wrapper p {
  margin: 0 2.5rem;
  font-size: 14px;
  font-weight: 500;
}
.product-section .container .title-wrapper h1 {
  margin: 0 2.5rem;
  font-weight: 700;
  font-size: 2rem;
  color: #5ccfd3;
}